.pic {
  width: 18%;
  border-radius: 50%;
  /* height: 10%; */
}
.candidateSuggestionCenter {
  display: flex;
  /* border: purple solid 2px; */
  justify-content: right;
}
.white:hover {
  color: blue;
}
#candidate {
  border-bottom: solid black 2px;
}
.profileForm {
  border-radius: 1%;
  background-color: rgba(255, 255, 255, 0.829);
  display: inline-block;
  width: 40%;
  height: auto;
  justify-content: left;
  border: 2px white solid;
