  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.profileInput {
  border: none;
  margin-bottom: 10px;
  width: 100%;
  text-align: center;
  font-size: 16px;
  padding: 5px 0;
}
